import { Box } from "@mui/material";
import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import SideBar from "../../components/SideBar/SideBar";
import classes from "./Layout.module.scss";
import Dashboard from "../../pages/Dashboard";
import Analytics from "../../pages/Analytics";
import Community from "../../pages/Community";
import Explore from "../../pages/Explore";

const MLayout = ({profileDetails, logout}) => {
  return (
    <Box className={classes.boxMainContainer}>
      <Router>
        <Box className={classes.sideBarMainContainer}>
          <SideBar profileDetails={profileDetails} logout={logout}/>
        </Box>
        <Box className={classes.mainPageContainer}>
          <Routes>
            <Route path="/dashboard" element={<Dashboard />} />
            <Route path="/analytics" element={<Analytics />} />
            <Route path="/community" element={<Community />} />
            <Route path="/explore" element={<Explore />} />
          </Routes>
        </Box>
      </Router>
    </Box>
  );
};

export default MLayout;
