import { Box, Divider } from "@mui/material";
import React, { useState } from "react";
import Search from "../../components/Search/Search";
import classes from "./Sidebar.module.scss";
import OtherHousesOutlinedIcon from "@mui/icons-material/OtherHousesOutlined";
import AutoGraphOutlinedIcon from "@mui/icons-material/AutoGraphOutlined";
import ForumOutlinedIcon from "@mui/icons-material/ForumOutlined";
import ExploreOutlinedIcon from "@mui/icons-material/ExploreOutlined";
import { Link } from "react-router-dom";

const SideBar = ({profileDetails, logout}) => {
  const [indexValuebg, setIndexColorbg] = useState(0);
  const handlePageNavigation = (index) => {
    setIndexColorbg(index);
  };
  const SideBarNav = [
    {
      icon: <OtherHousesOutlinedIcon />,
      title: "Dashboard",
      link: "/dashboard",
    },
    {
      icon: <AutoGraphOutlinedIcon />,
      title: "Analytics",
      link: "/analytics",
    },
    {
      icon: <ForumOutlinedIcon />,
      title: "Community",
      link: "/community",
    },
    {
      icon: <ExploreOutlinedIcon />,
      title: "Explore",
      link: "/explore",
    },
  ];
  return (
    <Box className={classes.sideBarItem}>
      <Box className={classes.appNameContainer}>
        <div className={classes.appLogo}>ns.</div>

        <Box>
          <div className={classes.companyName}>Nutri Scale</div>
          <div className={classes.companySlogan}>Scale your health</div>
        </Box>
      </Box>

      <Search />

      <Divider />

      <Box className={classes.sideFlexContainer}>
        <Box className={classes.btnOuterContainer}>
          {SideBarNav.map((btnRes, index) => {
            return (
              <Link
                className={classes.btnItemContainer}
                key={index}
                to={btnRes.link}
                style={{
                  backgroundColor: index === indexValuebg ? "#a1a1a1" : "",
                }}
                onClick={()=>handlePageNavigation(index)}
              >
                {btnRes.icon}
                <div>{btnRes.title}</div>
              </Link>
            );
          })}
        </Box>

        <Box className={classes.userContainer}>
          <img src={profileDetails.picture} alt="" className={classes.userImage} />
          {/* <div className={classes.userImage}>ns.</div> */}

          <Box>
            <div className={classes.userName}>{profileDetails.name}</div>
            <div className={classes.signOutBtn} onClick={logout}>Sign out {"->"}</div>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default SideBar;
