import { Box, Button } from "@mui/material";
import React from "react";
import classes from "./dashboard.module.scss";
import NotificationsOutlinedIcon from '@mui/icons-material/NotificationsOutlined';
import CalendarMonthOutlinedIcon from '@mui/icons-material/CalendarMonthOutlined';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import LineAreaBarChart from "../../components/Chart/LineAreaBarChart";
import LineChartGraph from "../../components/Chart/LineChart";
import PlayCircleOutlineOutlinedIcon from '@mui/icons-material/PlayCircleOutlineOutlined';

const Dashboard = () => {

    return(
        <Box className={classes.dashboardMainContainer}>

            <Box className={classes.dashboardNavBar}>
                <div className={classes.titleName}>Dashboard</div>
                <Box className={classes.btnGroup}>
                    <Button className={classes.btnOutItem}>
                        <NotificationsOutlinedIcon sx={{color:"#333"}} />
                    </Button>
                    <Button className={classes.btnOutItem}>
                        <CalendarMonthOutlinedIcon sx={{color:"#333"}} />
                    </Button>
                    <Button className={classes.btnFillItem}>
                        <AddOutlinedIcon /> Add activity
                    </Button>
                </Box>
            </Box>

            <Box className={classes.graphContainer}>
                <Box className={classes.graphItemBox}>
                    <LineAreaBarChart />
                </Box>

                <Box className={classes.graphItemBoxb}>
                    <LineChartGraph />
                </Box>

                <Box className={classes.graphItemBoxc}>
                    <Box className={classes.topBtnContainer}>
                        <Button className={classes.popularBtnItem}>
                            Popular Episode
                        </Button>
                        <Button className={classes.playBtnItem}>
                            <PlayCircleOutlineOutlinedIcon />
                        </Button>
                    </Box>
                    <Box className={classes.graphItemBoxcFooter}>
                        <div>How stress and emotions affect overall health</div>
                    </Box>
                </Box>
            </Box>

            {/* <Box>
                <div>Workouts Schedule</div>
            </Box> */}

            <Box>
                <div>Latest Activities</div>

                
            </Box>
        </Box>
    );
}

export default Dashboard;